<template>
    <div id="cart">
        <cart-item 
        v-for="(item, index) in CART" :key="item.id" 
        :cart_item_data="item" 
        @deleteFromCart="deleteFromCart(index)"
        @addQty="addItemQty(index)"
        @minusQty="minusItemQty(index)"
        />
        <div v-if="CART.length" id="cartInfo">
            <p>Всего {{CART.length}} на сумму {{totalPrice}}</p>
            <button >Заказать</button>
        </div>
        
    </div>
</template>
<script>
import{mapActions, mapGetters} from 'vuex'
import CartItem from '../components/CartItem'
export default {
    name: 'Cart',
    components:{CartItem},
    computed: {
        ...mapGetters(['CART']),
        totalPrice(){
            let result = 0
            for (let index in this.CART){
                result += this.CART[index].price * this.CART[index].qty;
            }
            return result
        }
    },
    methods:{
        ...mapActions(['REMOVE_FROM_CART', 'ADD_CART_ITEM_QTY', 'MINUS_CART_ITEM_QTY']),
        deleteFromCart(index){
            this.REMOVE_FROM_CART(index)
        },
        addItemQty(index){
            this.ADD_CART_ITEM_QTY(index)
        },
        minusItemQty(index){
            this.MINUS_CART_ITEM_QTY(index)
        }
    },
}
</script>
<style scoped>
    #cart{
        max-width: 1400px;
        margin: 0 auto;
    }
    #cartInfo button{
        padding: 10px 20px;
        background: #89B837;
        border: none;
        color: #fff;
        outline: none;
        cursor: pointer;
        text-transform: uppercase;
        font-size: 20px;
        margin-top: 10px;
    }
    #cartInfo button:hover{
        background: #628526;
    }
    
    
    
</style>